const PATHS = require('../paths');
const ExtractTextPlugin = require('extract-text-webpack-plugin');

module.exports = ({ production = false, browser = false } = {}) => {
  const CSS_LOADER = production ? 'happypack/loader?id=css-loader' : 'happypack/loader?id=css-loader?sourceMap&-minimize';
  const SASS_LOADER = production ? 'happypack/loader?id=sass-loader' : 'happypack/loader?id=sass-loader?sourceMap';

  return {
    test: /\.scss$/,
    use: ExtractTextPlugin.extract({
      fallback: "happypack/loader?id=style-loader",
      use: [`${CSS_LOADER}`, 'happypack/loader?id=postcss-loader', `${SASS_LOADER}`]
    }),
    include: [
      PATHS.src
    ]
  };
};
